@import url(https://fonts.googleapis.com/css2?family=Notable&family=Share+Tech+Mono&display=swap);
body {
  margin: 10px;
  background-color: rgba(111, 111, 171,);
  
}

.card-container{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  background-color: #9a8fbd;
}

.form-row{
  
  width: 38rem;
  display: inline-block;
}
.form-r{ 
  margin-left: 20px;
  margin-right: 20px;
}

.navbar {
  display: flex;
  justify-content: space-between;
  padding-left: 20px;
  padding-top: 20px;
  padding-right: 20px;
  background-color: #9a8fbd;
}


.balance {
  color: #5A34D5;
  font-family:  Mulish;
  font-size: x-large;
  list-style: none;
  text-decoration: none;
}

.card-title {
  color: #5A34D5;
  font-family: Mulish;
}

.brand-name {
  color: #5A34D5;
  font-family: Mulish;
  font-size: large;
 
}

.btn{
  margin-left: 8px;
}

.tm{
  color: white;
}

.card{
  width: 25rem;
  margin: 20px;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgba(255, 255, 255, 0.085);
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

